import React, { useState, useEffect, useContext, createContext } from "react";
import { Link } from "react-router-dom";

export default function Navbar(props) {
    const nav = 'nav ';
    function changeTheme() {
        props.theme === "light" ?  props.setTheme("dark") : props.setTheme("light");
    }

    const icon = props.theme === "light" ? 
        <img alt="" src="data:image/svg+xml;base64," height="22" width="22"/> : 
        <img alt="" src="data:image/svg+xml;base64,"  height="22" width="22"/>;

    useEffect(() => {
        window.addEventListener('scroll', handleScroll)
        // window.addEventListener("handleDarkMode", toogleDarkMode)
        function handleScroll() {
            if(window.scrollY) {
                document.querySelector('nav').className = 'nav scroll';
            } else {
                document.querySelector('nav').className = nav;
            }
        }
    }, []);
           
   return (
       <>
            <nav className={nav + ' navbar-default'}>
                <div className="nav-container">
                        <div className="brand">
                        <a href="svngoku.io/"> <img alt="logo" src="https://d1fdloi71mui9q.cloudfront.net/kSJ9SThSZ2vYvyXCbELw_2.png" width="40" height="40" /> </a>
                        </div>
                    <div className="links">
                            <Link to="/">About</Link> 
                            <Link to="/playground">Playground</Link>
                            {/* <Link to="/blogs">Blogs</Link>
                            <Link to="/blog">Blog</Link> */}
                            <button className="dark-switcher" onClick={changeTheme}>
                                {icon}
                            </button>
                        </div>
                </div> 
            </nav>
            <br />
            <br />
            <br />
       </>
   )
}